<template>
  <div class="container text-center">
    <h1 class="my-5">Iniciar Sesion</h1>
    <div id="login">
        <img id="img" src="../assets/icon.png" class="mx-2"/>
      <b-form @submit.prevent="login()">
        <b-form-group label="GamerTag:" class="my-2">
          <b-form-input v-model="usuario.usuario_user" placeholder="Ingresa tu gamertag"></b-form-input>
        </b-form-group>

        <b-form-group label="Contrase単a:" class="my-4">
          <b-form-input
            v-model="usuario.password"
            type="password"
            placeholder="Ingresa tu contrase単a"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="success" class="mx-2 my-4">Iniciar sesion</b-button>
        <b-button :to="{name: 'Register'}" variant="primary">Registrarse</b-button>
      </b-form>
      <div v-if="validar !== ''">
        <b-alert
          :show="dismissCountDown"
          dismissible
          :variant="mensaje.color"
          @dismissed="dismissCountDown=0"
          @dismiss-count-down="countDownChanged"
        >{{mensaje.texto}}</b-alert>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      usuario: { usuario_user: "", password: "" },
      validar: "",
      dismissSecs: 3,
      dismissCountDown: 0,
      mensaje: { color: "", texto: "" },
      entrar: true,
    };
  },
  methods: {
    ...mapMutations(["obtenerUsuario", "obtenerRol"]),
    ...mapActions([
      "guardarUsuario",
      "leerToken",
      "leerRol",
      "cerrarSesion",
      "guardarRol",
    ]),
    login() {
      this.axios
        .post("/login", this.usuario)
        .then((res) => {
          const token = res.data.token;
          const rolUser = res.data.rolUser;
          this.guardarUsuario(token);
          this.guardarRol(rolUser);
        })
        .catch((e) => {
          console.log(e);
          this.validar = e.response;
          (this.mensaje.color = "danger"),
            (this.mensaje.texto = e.response.data),
            this.showAlert();
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },
};
</script>

<style>
#login {
  display: flex;
  justify-content: center;
}
#img {
  justify-content: left;
}
</style>